export default function OddsList({ results }) {
  if (!results || results.length === 0) {
    return (
      <p className="text-gray-500 text-center mt-10">
        No odds available. Your API key is probably crying again.
      </p>
    );
  }

  return (
    <div className="space-y-6">
      {results.map((game, idx) => (
        <div key={idx} className="card">
          <h3 className="text-lg font-semibold mb-1 neon">
            {game.home_team} @ {game.away_team}
          </h3>

          <p className="text-xs text-gray-400 mb-3">
            Start: {game.commence_time}
          </p>

          <div className="grid grid-cols-3 gap-3 text-sm">
            {game.bookmakers.map((bm, i) => (
              <div key={i} className="bg-[#161618] p-3 rounded-lg">
                <p className="font-semibold mb-1 text-gray-300">{bm.title}</p>

                {bm.markets?.[0]?.outcomes?.map((o, j) => (
                  <p key={j} className="text-gray-400">
                    {o.name}: <span className="neon">{o.price}</span>
                  </p>
                ))}
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
}
